  div.item #timeline .expanded .addition {
    display: block; }
  div.item #timeline .expanded .highlight::after {
    display: none; }
  div.item #timeline .title {
    font-size: 1.6em;
    font-weight: 300;
    text-transform: uppercase;
    position: relative;
    top: -0.3em;
    margin: 0.3em 0; }
  div.item #timeline .highlight, div.item #timeline .addition {
    line-height: 1.5em; }
  div.item #timeline .highlight::after {
